import React from 'react'
import { Component } from 'react'
import shortid from 'shortid'

class CarouselControls extends Component {

    handleClick (e, i) {
        e.preventDefault()
        this.props.handleClick(i)
    }

    render () {
        const { count, handleClick, activeIndex } = this.props
        const controls = []

        for (let i = 0; i < count; i++) {
            controls.push(<button
                key={shortid.generate()}
                className={activeIndex === i ? 'active' : null}
                onClick={(e) => this.handleClick(e, i)}>{i}
            </button>)
        }
        return <div className="carousel__controls">{controls}</div>
    }
}

export default CarouselControls


