import React from 'react'
import { Component } from 'react'
import shortid from 'shortid'

import CarouselSlide from './carousel-slide'
import CarouselControls from './carousel-controls'

class Carousel extends Component {
    constructor(props) {
        super()

        if (!props.slides) {
            throw new Error('Carousel component missing props.slides')
        }

        this.state = {
            activeIndex: 0,
            gotoIndex: 0,
            styleRight: {},
            styleLeft: {},
            styleCarousel: {},
            count: props.slides.length,
            animating: false,
            direction: ''
        }
        this.animationDur = 750
    }

    getCarouselHeight() {
        return this.carouselEl.getBoundingClientRect().height
    }

    handleClick(gotoIndex) {
        if (this.state.animating || gotoIndex === this.state.activeIndex) return

        let { count, activeIndex } = this.state
        const animating = true
        const halfHeight = (this.getCarouselHeight() / 2)
        const direction = gotoIndex > activeIndex ? 'fwd' : 'bck'

        const styleLeft = {
            transform: `translateZ(-${halfHeight}px) rotateX(${(direction === 'fwd') ? '90' : '-90'}deg)`,
            'transition': `all ${this.animationDur}ms`
        }

        const styleRight = {
            transform: `translateZ(-${halfHeight}px) rotateX(${(direction === 'fwd') ? '-90' : '90'}deg)`,
            'transition': `all ${this.animationDur}ms`
        }

        this.setState({ styleLeft, styleRight, gotoIndex, animating, direction })

        // update once the animation is complete
        setTimeout(() => {
            this.updateProperties()
        }, this.animationDur)
    }

    updateProperties() {
        const activeIndex = this.state.gotoIndex
        const animating = false
        const halfHeight = this.getCarouselHeight() / 2
        const styleLeft = { transform: `translateZ(-${halfHeight}px)`, 'transition': 'none' }
        const styleRight = { transform: `translateZ(-${halfHeight}px)`, 'transition': 'none' }

        this.setState({ activeIndex, animating, styleLeft, styleRight })
    }

    render() {
        const { activeIndex, count, gotoIndex, direction } = this.state
        const { slides } = this.props

        return (
            <div>
                <div className="carousel" ref={(el) => this.carouselEl = (el)} style={this.state.styleCarousel}>
                    <CarouselControls activeIndex={gotoIndex === activeIndex ? activeIndex : gotoIndex} count={slides.length} handleClick={this.handleClick.bind(this)} />

                    <div className="carousel__spinner" data-left style={this.state.styleLeft}>
                        {slides && slides.map((val, index) => {
                            return <CarouselSlide key={shortid.generate()} index={index} slide={val} activeIndex={activeIndex} gotoIndex={gotoIndex} count={count} direction={direction} />
                        })}
                    </div>
                    <div className="carousel__spinner" data-right style={this.state.styleRight}>
                        {slides && slides.map((val, index) => {
                            return <CarouselSlide key={shortid.generate()} index={index} slide={val} activeIndex={activeIndex} gotoIndex={gotoIndex} count={count} direction={direction} />
                        })}
                    </div>
                </div>
            </div>
        )
    }
}

export default Carousel


