import React from 'react'
import { Component } from 'react'

class Slide extends Component {
    render() {
        const { index, activeIndex, count, gotoIndex, direction, loc, slide} = this.props || false
        const next = gotoIndex === index && index !== activeIndex && direction === 'fwd'
        const prev = gotoIndex === index && index !== activeIndex && direction === 'bck'
        const active = index === activeIndex

        return (
            <div className="slide" data-next={next} data-prev={prev} data-active={active} data-loc={loc}>
                <div className="slide__mask">
                    <div className="slide__content">{slide}</div>
                </div>
            </div>
        )
    }
}

export default Slide


